.main-title {
  font-family: 'Poppins SemiBold', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: '#14395E';
}

.table-container {
  margin: 43.5px 0 36px 0;

  h3 {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-size: 22px;
    line-height: 33px;
    margin-bottom: 18px;
  }
}

.btn-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  a {
    text-decoration: none;
  }
}

.addBtn {
  background: #1f3bb3 0% 0% no-repeat padding-box;
  border-radius: 10px;
  color: #ffffff;
  font-size: 18px;
  line-height: 27px;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  height: 50px;
  text-transform: capitalize;
  width: 316px;
  padding: 13px 0 12px 0;

  svg {
    color: #ffffff;
  }
}

.addBtn:hover {
  background: #3833df;
}
